.profile-sidebar {
  width: 300px;
  margin-left: 2rem;
}

.profile-card {
  background: var(--background-secondary);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 24px var(--shadow-color);
  position: sticky;
  top: 100px;
}

.avatar {
  width: 100px;
  height: 100px;
  margin: 0 auto 1rem;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 4px 12px var(--shadow-color);
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-card h2 {
  text-align: center;
  color: var(--text-color);
  margin-bottom: 0.5rem;
}

.bio {
  text-align: center;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

.stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 1.5rem;
}

.stat-item {
  text-align: center;
}

.stat-value {
  display: block;
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--primary-color);
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.tech-stack {
  margin-bottom: 1.5rem;
}

.tech-stack h3 {
  color: var(--text-color);
  margin-bottom: 0.75rem;
  font-size: 1rem;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tags span {
  background: var(--code-background);
  color: var(--primary-color);
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 0.85rem;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.social-links a {
  color: var(--text-color);
  text-decoration: none;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: var(--primary-color);
}

@media (max-width: 1280px) {
  .profile-sidebar {
    display: none;
  }
} 