.not-found {
  text-align: center;
  padding: 100px 20px;
  max-width: 600px;
  margin: 0 auto;
}

.not-found h1 {
  font-size: 6rem;
  color: var(--primary-color);
  margin: 0;
  line-height: 1;
}

.not-found h2 {
  font-size: 2rem;
  color: var(--text-color);
  margin: 1rem 0;
}

.not-found p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.back-home {
  display: inline-block;
  padding: 12px 24px;
  background: var(--primary-color);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.back-home:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
} 