.privacy-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
}

.privacy-content {
  background: var(--background-secondary);
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 4px 24px var(--shadow-color);
}

.privacy-content h1 {
  color: var(--text-color);
  margin-bottom: 2rem;
}

.privacy-content section {
  margin-bottom: 2rem;
}

.privacy-content h2 {
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.privacy-content ul {
  list-style: none;
  padding-left: 0;
}

.privacy-content li {
  margin: 0.5rem 0;
  padding-left: 1.5rem;
  position: relative;
  color: var(--text-color);
}

.privacy-content li::before {
  content: "•";
  position: absolute;
  left: 0;
  color: var(--primary-color);
}

.privacy-content a {
  color: var(--primary-color);
  text-decoration: none;
}

.privacy-content a:hover {
  text-decoration: underline;
}

.privacy-content p {
  color: var(--text-color);
}

@media (max-width: 768px) {
  .privacy-container {
    padding: 20px;
  }
  
  .privacy-content {
    padding: 20px;
  }
} 