.scroll-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  background: var(--background-secondary);
  color: var(--text-color);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px var(--shadow-color);
  z-index: 1000;
}

.scroll-top.visible {
  opacity: 1;
  visibility: visible;
}

.scroll-top:hover {
  background: var(--primary-color);
  color: white;
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .scroll-top {
    bottom: 15px;
    right: 15px;
    width: 35px;
    height: 35px;
    font-size: 18px;
  }
} 