.post-container {
  position: relative;
  max-width: 800px;
  margin: 40px auto;
  padding: 24px;
  background: var(--background-secondary);
  border-radius: 16px;
  box-shadow: 0 4px 24px var(--shadow-color);
}

.post {
  background: var(--background-secondary);
  color: var(--text-color);
}

.post-header {
  text-align: center;
  margin-bottom: 24px;
  background: var(--background-secondary);
  color: var(--text-color);
}

.post-header h1 {
  color: var(--text-color);
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: bold;
}

.post-meta {
  color: var(--text-secondary);
  font-size: 0.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
  background: var(--background-secondary);
}

.post-meta span,
.post-meta .dot,
.post-meta .date,
.post-meta .author,
.post-meta .read-time {
  color: var(--text-secondary);
  background: var(--background-secondary);
}

.tags {
  display: flex;
  gap: 8px;
  justify-content: center;
  margin-top: 1rem;
  flex-wrap: wrap;
  background: var(--background-secondary);
}

.tag {
  background: var(--code-background);
  color: var(--primary-color);
  padding: 4px 12px;
  border-radius: 16px;
  font-size: 0.85rem;
}

.post-content {
  background: var(--background-secondary);
}

.post-content > * {
  color: var(--text-color);
}

.post-content > *:not(pre):not(code) {
  color: var(--text-color);
}

.back-button {
  position: fixed;
  top: 100px;
  left: 20px;
  width: 40px;
  height: 40px;
  padding: 0;
  background: var(--background-secondary);
  color: var(--text-color);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  z-index: 1000;
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px var(--shadow-color);
}

.back-button:hover {
  background: var(--primary-color);
  color: white;
  transform: scale(1.1);
}

@media (min-width: 1280px) {
  .post-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 900px;
  }
}

@media (max-width: 768px) {
  .post-container {
    margin: 20px;
    padding: 20px;
    max-width: calc(100% - 40px);
  }
  
  .back-button {
    top: 70px;
    left: 15px;
    width: 35px;
    height: 35px;
    font-size: 18px;
  }
}

/* 文章内容区域 */
.post-content.markdown-content {
  background: var(--background-secondary);
  color: var(--text-color);
}

/* 强制所有文本元素使用主题颜色 */
.post-content.markdown-content > * {
  color: var(--text-color) !important;
}

.post-content.markdown-content h1,
.post-content.markdown-content h2,
.post-content.markdown-content h3,
.post-content.markdown-content h4,
.post-content.markdown-content h5,
.post-content.markdown-content h6,
.post-content.markdown-content p,
.post-content.markdown-content li,
.post-content.markdown-content span {
  color: var(--text-color) !important;
  background: var(--background-secondary);
}

/* 代码块样式 */
.markdown-content pre {
  background-color: var(--code-background);
  color: var(--code-text);
  padding: 12px;
  border-radius: 8px;
  overflow: auto;
  font-size: 0.9em;
  line-height: 1.5;
}

/* 行内代码样式 */
.markdown-content code {
  background-color: var(--code-background);
  color: var(--code-text);
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 85%;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
}

/* 图片容器样式 */
.image-container {
  position: relative;
  width: 100%;
  margin: 1rem 0;
}

.image-container img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 2px 8px var(--shadow-color);
}

/* 确保图片不会溢出容器 */
.post-content.markdown-content {
  overflow-x: hidden;
}