:root {
  /* 浅色主题变量 */
  --primary-color: #2dd4bf;
  --primary-hover: #14b8a6;
  --background-color: #f8fafb;
  --background-secondary: #ffffff;
  --text-color: #334155;
  --text-secondary: #64748b;
  --border-color: #e2e8f0;
  --blockquote-color: #94a3b8;
  --blockquote-border: #cbd5e1;
  --shadow-color: rgba(45, 212, 191, 0.1);
  --accent-color: #f59e0b;
  --code-background: #f6f8fa;  /* 浅灰色背景 */
  --code-text: var(--primary-color);  /* 使用主题色作为文字颜色 */
}

/* 深色主题变量 - 温暖的深色调 */
.dark-theme {
  --primary-color: #22c55e;
  --primary-hover: #16a34a;
  --background-color: #1e293b;
  --background-secondary: #293548;
  --text-color: #f1f5f9;
  --text-secondary: #cbd5e1;
  --border-color: #3e4c63;
  --blockquote-color: #94a3b8;
  --blockquote-border: #475569;
  --shadow-color: rgba(0, 0, 0, 0.15);
  --accent-color: #fbbf24;
  --code-background: #1a2234;  /* 深一点的背景色 */
  --code-text: var(--primary-color);  /* 使用主题色作为文字颜色 */
}

/* 平滑过渡 */
* {
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .post-container {
    padding: 20px;
    margin: 20px;
  }
} 

/* 通用样式 */
.dot {
  color: #999;
  margin: 0 8px;
}

.read-more {
  display: inline-block;
  margin-top: 16px;
  padding: 8px 16px;
  background-color: var(--primary-color);
  color: white;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.read-more:hover {
  background-color: #0052a3;
}

/* 滚动条美化 */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--background-color);
}

::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--text-secondary);
}

/* 文本选择样式 */
::selection {
  background: var(--primary-color);
  color: white;
} 

/* 为代码块添加例外，防止颜色过渡动画 */
pre *, code * {
  transition: none !important;
} 