.search-result-item {
  display: block;
  padding: 1rem;
  border-radius: 8px;
  background: var(--background-secondary);
  color: var(--text-color);
  text-decoration: none;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
}

.search-result-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
}

.search-result-item h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
  color: var(--text-color);
}

.search-result-item p {
  margin: 0 0 0.5rem 0;
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.5;
}

.search-meta {
  font-size: 0.8rem;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 8px;
}

.no-results {
  padding: 2rem;
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.dot {
  color: var(--text-secondary);
}