.header {
  background: var(--background-secondary);
  border-bottom: 1px solid var(--border-color);
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.header .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .logo {
  color: var(--text-color);
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: bold;
  transition: color 0.3s ease;
}

.header .logo:hover {
  color: var(--primary-color);
}

.header nav ul {
  display: flex;
  gap: 2rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

.header nav a {
  color: var(--text-color);
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease;
}

.header nav a:hover {
  color: var(--primary-color);
}

@media (max-width: 768px) {
  .header {
    padding: 0.8rem 0;
  }

  .header .container {
    padding: 0 15px;
  }

  .header .logo {
    font-size: 1.2rem;
  }

  .header nav ul {
    gap: 1rem;
  }

  .header nav a {
    font-size: 0.9rem;
  }
} 