.about-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
}

.about-header {
  text-align: center;
  margin-bottom: 40px;
}

.avatar {
  width: 150px;
  height: 150px;
  margin: 20px auto;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 4px 20px var(--shadow-color);
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-content {
  background: var(--background-secondary);
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 4px 24px var(--shadow-color);
}

.intro-section,
.skills-section,
.interests-section,
.contact-section {
  margin-bottom: 40px;
}

h2 {
  color: var(--primary-color);
  margin-bottom: 20px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.skill-item {
  background: var(--background-color);
  padding: 20px;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.skill-item:hover {
  transform: translateY(-5px);
}

.skill-name {
  font-weight: 600;
  color: var(--text-color);
  display: block;
  margin-bottom: 10px;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.skill-tags span {
  background: var(--code-background);
  padding: 4px 12px;
  border-radius: 16px;
  font-size: 0.9rem;
  color: var(--primary-color);
}

.interests-section ul {
  list-style: none;
  padding: 0;
}

.interests-section li {
  margin: 10px 0;
  padding-left: 24px;
  position: relative;
}

.interests-section li::before {
  content: "→";
  position: absolute;
  left: 0;
  color: var(--primary-color);
}

.contact-links {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.contact-links a,
.contact-links button {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-color);
  text-decoration: none;
  padding: 8px 16px;
  border-radius: 20px;
  background: var(--background-color);
  transition: all 0.3s ease;
  font-size: 1rem;
  border: none;
  cursor: pointer;
}

.contact-links a:hover,
.contact-links button:hover {
  background: var(--primary-color);
  color: white;
}

@media (max-width: 768px) {
  .about-container {
    padding: 20px;
  }
  
  .about-content {
    padding: 20px;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .contact-links {
    flex-direction: column;
  }
} 