.blog-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.post-item {
  background: var(--background-secondary);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 24px var(--shadow-color);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-height: 280px;
  display: flex;
  flex-direction: column;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
}

.post-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--primary-color);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.post-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 32px rgba(0,0,0,0.1);
}

.post-item:hover::before {
  opacity: 1;
}

.post-meta {
  display: flex;
  gap: 1rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 0.75rem;
}

.post-title {
  font-size: 1.5rem;
  margin-bottom: 0.75rem;
  color: var(--text-color);
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.post-excerpt {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  flex: 1;
}

.post-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.post-tags {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  max-width: 70%;
}

.post-tag {
  background: var(--code-background);
  color: var(--primary-color);
  padding: 4px 12px;
  border-radius: 16px;
  font-size: 0.85rem;
}

.read-more {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  transition: transform 0.3s ease;
  display: inline-flex;
  align-items: center;
}

.read-more:hover {
  transform: translateX(4px);
}

@media (max-width: 768px) {
  .post-item {
    padding: 1.5rem;
    width: calc(100% - 2rem);
    margin: 0 1rem;
  }
  
  .post-footer {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
} 